


























import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class StatisticsCard extends Vue {
    /**
     * Loading.
     */
    @Prop({ default: false })
    loading: boolean;

    /**
     * Statistic value.
     */
    @Prop()
    value: number;

    /**
     * Statistic title.
     */
    @Prop()
    title: string;

    /**
     * Color of the card.
     */
    @Prop({ default: "primary" })
    color: string;

    /**
     * If the card is flat or not.
     */
    @Prop({ default: false })
    flat: boolean;
}
